body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.image{
  position: relative;
}
.backgroundImage1{
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1; 
  display: inline;
  /* margin-top: -192px; */
}

.card{
  display: inline;
  position: relative;
}
.card1{
  z-index: 2; 
  margin-top: 76px;
  margin-left: 76px;
}

.card2{
  z-index: 2; 
  display: block;
  margin-top: 120px;
  margin-left: 190px;
}